const line = function (id) {
  var dom = document.getElementById(id)
  var myChart = echarts.init(dom)
  var option = {
    xAxis: {
      type: 'category',
      data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
    },
    yAxis: {
      type: 'value'
    },
    series: [{
      data: [820, 932, 901, 934, 1290, 1330, 1320],
      type: 'line'
    }]
  }
  myChart.setOption(option)
}

const chinaMap = function (id, data) {
  const dom = document.getElementById(id)
  const myEcharts = echarts.init(dom)
  const option = {
    // 
    tooltip: {
      // 
      triggerOn: 'click',
      // false true
      enterable: true,
      // data 
      formatter(data) {
        return `<div>
          <a href='/covid19/#/city/${data.name}' style='color: #fff'>
            <p>${data.name}</p>
            <p>:${data.value}</p>
          </a>
        </div>`
      }
    },
    // 
    visualMap: [{
      orient: 'vertical', // :horizontal
      type: 'piecewise', // 
      pieces: [ // 
        { min: 0, max: 0, color: '#FFFFFF' },
        { min: 1, max: 10, color: '#FDFDCF' },
        { min: 10, max: 100, color: '#FE9E83' },
        { min: 100, max: 500, color: '#E55A4E' },
        { min: 500, max: 10000, color: '#4F070D' }
      ]
    }],
    // 
    series: [{
      // 
      name: '',
      type: 'map', // 
      map: 'china', // 
      roam: true, // 
      zoom: 1.1, // 
      // 
      label: {
        // normal
        normal: {
          // 
          show: true,
          // 
          textStyle: {
            fontSize: 8
          }
        }
      },
      // 
      itemStyle: {
        // normal
        normal: {
          areaColor: 'rgba(0,255,236,0)',
          borderColor: 'rgba(0,0,0,0.2)'
        },
        // 
        emphasis: {
          areaColor: 'rgba(255,180,0,0.8)',
          shadowOffsetX: 0,
          shadowOffsetY: 0,
          // 
          shadowBlur: 15,
          // 
          shadowColor: 'rgba(0, 0, 0, 0.5)',
          // 
          borderWidth: 1
        }
      },
      data
      // 
      // data: [
      //   // name value 
      //   { name: '', value: 10, itemStyle: { normal: { areaColor: '#f00' } } }
      // ]
    }]
  }
  myEcharts.setOption(option)
}

const cityMap = function (id, cityName, data) {
  const dom = document.getElementById(id)
  const myEcharts = echarts.init(dom)
  const option = {
    tooltip: {
      // 
      triggerOn: 'click',
      enterable: true,
      formatter(data) {
        return '<div><p>' + data.name + '</p><p>:' + data.value + '</p></div>'
      }
    },
    visualMap: [{
      orient: 'vertical', // :horizontal
      type: 'piecewise', // 
      pieces: [ // 
        { min: 0, max: 0, color: '#FFFFFF' },
        { min: 1, max: 10, color: '#FDFDCF' },
        { min: 10, max: 100, color: '#FE9E83' },
        { min: 100, max: 500, color: '#E55A4E' },
        { min: 500, max: 10000, color: '#4F070D' }
      ]
    }],
    // 
    series: [{
      // 
      name: '',
      type: 'map', // 
      map: cityName, // 
      roam: true, // 
      zoom: 1.1, // 
      // 
      label: {
        // normal
        normal: {
          // 
          show: true,
          // 
          textStyle: {
            fontSize: 10
          }
        }
      },
      // 
      itemStyle: {
        // normal
        normal: {
          areaColor: 'rgba(0,255,236,0)',
          borderColor: 'rgba(0,0,0,0.2)'
        },
        // 
        emphasis: {
          areaColor: 'rgba(255,180,0,0.8)',
          shadowOffsetX: 0,
          shadowOffsetY: 0,
          // 
          shadowBlur: 15,
          // 
          shadowColor: 'rgba(0, 0, 0, 0.5)',
          // 
          borderWidth: 1
        }
      },
      data
    }]
  }
  myEcharts.setOption(option)
}

const worldMap = function (id, data) {
  const dom = document.getElementById(id)
  const myChart = echarts.init(dom)
  const option = {
    // 
    tooltip: {
      // 
      triggerOn: 'click',
      enterable: true,
      formatter(data) {
        return '<div><p>' + data.name + '</p><p>:' + data.value + '</p></div>'
      }
    },
    visualMap: [{ // -
      orient: 'vertical', // :horizontal
      type: 'piecewise', // 
      pieces: [ // 
        { min: 0, max: 0, color: '#FFFFFF' },
        { min: 1, max: 1000, color: '#FDFDCF' },
        { min: 1000, max: 10000, color: '#FE9E83' },
        { min: 10000, max: 500000, color: '#E55A4E' },
        { min: 500000, color: '#4F070D' }
      ]
    }],
    // 
    series: [{
      name: 'country',
      type: 'map', // 
      map: 'world', // 
      roam: true, // 
      zoom: 1.8, // 
      // 
      label: {
        // normal
        normal: {
          // 
          show: false,
          // 
          textStyle: {
            fontSize: 8
          }
        }
      },
      nameMap: nameMap,
      // 
      itemStyle: {
        // normal
        normal: {
          areaColor: 'rgba(0,255,236,0)',
          borderColor: 'rgba(0,0,0,0.2)'
        },
        // 
        emphasis: {
          areaColor: 'rgba(255,180,0,0.8)',
          shadowOffsetX: 0,
          shadowOffsetY: 0,
          // 
          shadowBlur: 15,
          // 
          shadowColor: 'rgba(0, 0, 0, 0.5)',
          // 
          borderWidth: 1
        }
      },
      data
    }],
  }
  myChart.setOption(option)
}

export default {
  install(Vue) {
    Object.defineProperty(Vue.prototype, '$charts', {
      get() {
        return {
          line,
          chinaMap,
          worldMap,
          cityMap
        }
      }
    })
  }
}
